@import "_variables.scss";

@import "_about-me.scss";
@import "_portfolio.scss";
@import "_contact.scss";

html {
  scroll-behavior: smooth;
}

body,
html {
  max-width: 100vw;
  margin: 0;
  padding: 0;

  color: $text-color;
  background-color: $background-color
    /*#1c1b3d*/
  ;
  background-attachment: fixed;
  font-family: "Geologica", sans-serif;

  transition: background-color $theme-transition-duration,
    color $theme-transition-duration;

  & * {
    scroll-margin-top: calc($navbar-height + 16px);
  }
}

body {
  background: $background-color;
  background-image: radial-gradient($border-color 2px, transparent 0);
  background-size: 20px 20px;
  background-position: -19px -19px;
}

a {
  cursor: pointer;
}

.background-canvas {
  position: fixed;
  left: 0;
  top: 0;

  width: 100vw;
  height: 100vh;

  opacity: 0.4;
  pointer-events: none;
  filter: var(--bg-canvas-filter);
}

#background-canvas {
  clip-path: rect($navbar-height 0 100% 100%);
}

#background-canvas-navbar {
  clip-path: rect(0 0 $navbar-height 100%);
  z-index: 500;
  opacity: 0.4;
}

main {
  overflow-y: scroll;
  max-height: 100vh;

  & a {
    color: $link-color;
  }
}

.section {
  position: relative;
  margin-bottom: 16px;
  padding-bottom: 20px !important;

  &.dark-background {
    border: 2px solid #eeeeee40;
    border-left: none;
    border-right: none;
    filter: drop-shadow(0 0 8px #eeeeee20);
  }

  &#contact {
    padding-bottom: 0 !important;
  }
}

.lighten {
  filter: brightness(2);
}

.dark-background {
  z-index: 100;

  background-color: $background-color; //#302d53;
  box-shadow: 0 0 16px #00000060;

  transition: background-color $theme-transition-duration;
}

.system-font {
  font-family: $system-sans;
}

.pad-sides {
  padding: 0 $padding-width;
}

.pad-sides-more {
  padding: 0 60px;
}

.clamp-width {
  margin: auto;
  max-width: $max-content-width;
}

.gradient {
  width: fit-content;

  background-image: $primary-gradient;
  background-clip: text;
  cursor: default;

  font-weight: 700;
  text-shadow: none !important;
  -webkit-text-fill-color: transparent;

  transition: transform 0.8s $super-ease-in-out;

  &.grow:hover {
    transform: scale(1.04);
  }
}

.gradient-underline {
  position: relative;
  width: fit-content;

  &::after {
    content: "";
    position: absolute;
    top: 100%;
    bottom: -2px;
    left: 0;
    width: 100%;

    background-image: $primary-gradient;
  }
}

.gradient-accent {
  $thickness: -4px;

  position: relative;
  width: fit-content;
  height: fit-content;

  &::after {
    content: "";
    position: absolute;
    background-color: $text-color;
    opacity: 0.8;
    z-index: 90;
    transition: background-color $theme-transition-duration;
  }

  &.glow::after {
    filter: drop-shadow(0 0 4px $text-color);
    transition: filter $theme-transition-duration;
  }

  &.right::after {
    left: 100%;
    right: $thickness;
    top: 0;
    height: 100%;
  }

  &.left::after {
    right: 100%;
    left: $thickness;
    top: 0;
    height: 100%;
  }

  &.bottom::after {
    top: 100%;
    bottom: $thickness;
    left: 0;
    width: 100%;
  }
}

.orange-border {
  border: 2px solid transparent;
  transition: border 0.5s $super-ease-in-out;

  &:hover {
    border: 2px solid #e23745;
    filter: drop-shadow(0 0 4px $accent-fade);
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes move-up {
  from {
    transform: translateY(20px);
  }

  to {
    transform: translateY(0%);
  }
}

.transparent {
  opacity: 0;
}

.header-animation {
  animation: fade-in 0.75s $super-ease-in, move-up 0.75s $super-ease-out;
  animation-iteration-count: 1;
}

.navbar {
  --backdrop-filter-opacity: 1;

  z-index: 400;

  position: fixed;
  height: $navbar-height;
  top: 0;
  left: 0;
  right: 0;

  margin: 0;
  padding: 0 20px;
  padding-bottom: $navbar-blur;

  &::before {
    content: "";
    z-index: -1;
    pointer-events: none;

    position: absolute;
    top: -20px;
    right: 0;
    bottom: 50%;
    left: 0;

    opacity: var(--backdrop-filter-opacity);
    background-color: $background-color;
    transition: background-color $theme-transition-duration;
  }

  &::after {
    content: "";
    z-index: -1;

    position: absolute;
    top: -20px;
    right: 0;
    bottom: 0;
    left: 0;

    opacity: var(--backdrop-filter-opacity);
    background-color: $background-color;
    transition: background-color $theme-transition-duration;
    filter: blur($navbar-blur);
  }
}

#navbar-name {
  position: relative;
  color: $text-color;
  transition: color $theme-transition-duration;

  z-index: 900;

  &::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -4px;
    width: 100%;
    height: 4px;
    transform: skew(-18deg);
    background: #e64a3f60;
    box-shadow: 0 0 12px #e64a3fa0;
    z-index: -1;
  }
}

#theme-button {
  border: none;
  background: none;
  z-index: 900;
  margin-left: 16px;
  cursor: pointer;
}

#theme-icon {
  filter: $invert-filter;
  margin-bottom: -4px;
}

.navbar-content {
  height: 100%;
  margin: auto;
  max-width: $navbar-width;
  display: flex;
  justify-content: stretch;
  align-items: center;

  &>h1 {
    font-weight: 500;
    margin-left: 20px;
  }

  nav {
    margin-left: auto;
    z-index: 900;
  }

  nav * {
    margin-left: 12px;
    color: $text-color !important;
    width: fit-content;
    background: linear-gradient(100deg,
        $text-color,
        $text-color 33%,
        #bd1321,
        #e23745,
        #e64a3f,
        #e66146);
    background-clip: text;
    background-size: 300% 100%;
    -webkit-text-fill-color: transparent;
    text-decoration: none;
    transition: color $theme-transition-duration;
    cursor: pointer;

    &:hover {
      background-position: top left 100%;
    }
  }
}

.hide-background {
  position: relative;
  --blur-filter: blur(20px);

  // &::before {
  // 	content: "";
  // 	position: absolute;
  // 	left: -32px;
  // 	top: -32px;
  // 	right: -32px;
  // 	bottom: -32px;

  // 	// background-color: $background-color;
  // 	opacity: 0.9;
  // 	filter: blur(20px);
  // 	-webkit-backdrop-filter: var(--blur-filter);
  // 	backdrop-filter: var(--blur-filter);
  // 	z-index: -1;
  // 	pointer-events: none;
  // }
}

.logo {
  aspect-ratio: 1;
  height: 70%;
  z-index: 1000;
  border-radius: 50%;
  filter: url(#noise);
}

.header {
  position: relative;
  margin: $navbar-height auto 1em auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: fit-content;
  min-height: min(calc(100vh - $navbar-height), 1200px);
}

.section-header {
  text-align: center;
  padding-top: 16px;
  margin: 0 auto 24px auto;
}

.title,
.subtitle {
  * {
    display: inline-block;
    margin: 0;
    margin-right: 16px;
  }
}

.title {
  font-size: 30pt;
  margin-bottom: 60px;
}

.subtitle {
  max-width: 450px;
  font-size: 18pt;
  text-align: left;
  margin-bottom: 60px + $navbar-height;
}

