#social-buttons {
  position: absolute;
  bottom: $navbar-height / 2;
  margin-bottom: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;

  a,
  a:link,
  a:hover,
  a:visited,
  a:active {
    color: $text-color;
    text-decoration: none;
    opacity: 0.7;
    transition: opacity 0.25s $super-ease-in-out,
      transform 0.8s $super-ease-in-out, color $theme-transition-duration;
    margin: 0 16px;

    &:hover {
      opacity: 1;
      transform: scale(1.15);
    }
  }

  i {
    font-size: $social-button-size;
  }
}

.social-button {

  &,
  &:link,
  &:hover,
  &:visited,
  &:active {
    color: $text-color;
    text-decoration: none;
    opacity: 0.7;
    transition: opacity 0.25s $super-ease-in-out,
      transform 0.8s $super-ease-in-out, color $theme-transition-duration;
    margin: 0 16px;
    cursor: pointer;

    &:hover {
      opacity: 1;
    }
  }

  i {
    font-size: $social-button-size;
  }
}

@keyframes arrow {
  from {
    top: -2px;
  }

  to {
    top: 2px;
  }
}

#learn-more-container {
  position: absolute;
  opacity: 0.7;
  left: 50%;
  transform: translate(-50%, 0);
  bottom: 20px;
  width: 100%;
  text-align: center;

  a {
    color: inherit;
    text-decoration: underline;
    text-decoration-color: transparent;
    transition: all 0.2s;

    &:hover {
      text-decoration-color: inherit;
    }
  }

  .arrow {
    font-size: 120%;
    left: 2px;
    position: relative;
    display: inline-block;
    animation-name: arrow;
    animation-duration: 1s;
    animation-timing-function: ease-in-out;
    animation-direction: alternate;
    animation-iteration-count: infinite;
  }
}

#skills {
  &>h2 {
    font-weight: 300;
    font-size: 24pt;
    text-align: center;
    margin-top: 0;
    margin-bottom: 1.5em;
  }
}

#skills-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.skill {
  margin: 8px 16px;
  display: flex;
  flex: 1 1 0px;
  flex-direction: column;
  align-items: center;
  transition: filter 0.9s $super-ease-in-out;
  transform: scale(1);
  filter: brightness(0.8);

  &:hover {
    filter: brightness(1);
  }

  &>img {
    min-width: 50px;
    min-height: 50px;
    width: auto;
    height: 6vw;
    max-width: 120px;
    max-height: 120px;
  }

  &>span {
    margin-top: 8px;
    position: relative;
    width: fit-content;
  }

  &>span::after {
    content: "";
    position: absolute;
    top: 100%;
    bottom: -2px;
    left: 0;
    width: 0;
    background-image: $primary-gradient;
    transition: all 0.2s $super-ease-out;
  }

  &:hover span::after {
    width: 100%;
  }
}

@media only screen and (max-width: 650px) {
  #navbar-name {
    display: none;
  }

  .title {
    font-size: 20pt;
  }

  .subtitle {
    font-size: 14pt;
  }
}
